:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Arial, sans-serif;
}
.will-change-transform {
  will-change: transform;
}
.animated-wave,
.parallax > use {
  will-change: transform;
}
section {
  margin-bottom: 20px;
}
.header-background {
  background-image: url("/bg-img-pages.svg");
  background-size: cover;
  background-position: center;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
:root {
  --gradient-percent: 0%;
}
body {
  margin: 0;
}
h1 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 48px;
  text-align: center;
}
p {
  font-family: 'Lato', sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  color: #333333;
}
.header {
  position: relative;
  text-align: center;
  background: url('/bg-img-pages.svg'), linear-gradient(90deg, rgba(0, 49, 84, .8) var(--gradient-percent), rgba(154, 198, 229, 1) 100%);
  background-size: cover;
  background-position: center;
  color: white;
}
.logo {
  width: 50px;
  fill: white;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
}
.inner-header {
  height: 35vh;
  width: 100%;
  margin: 0;
  padding: 0;
}
.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.waves {
  position: relative;
  width: 100%;
  height: 10vh;
  margin-bottom: -7px;
  min-height: 100px;
  max-height: 150px;
}
.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}
.parallax>use {
  animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}
.parallax>use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax>use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax>use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax>use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
  .content {
    height: 30vh;
  }
  h1 {
    font-size: 24px;
  }
}
.progress-circle-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.progress-circle {
  width: 80px;
  height: 80px;
}
.progress-circle-bar {
  fill: none;
  stroke: rgb(246, 137, 127);
  stroke-width: 5;
  stroke-dasharray: 283;
  stroke-dashoffset: var(--stroke-dashoffset, 0);
  transform-origin: center;
  transition: stroke-dashoffset 0.3s ease;
}
.progress-background {
  fill: transparent;
  stroke: #ededed;
  stroke-width: 5;
  stroke-dasharray: none;
}
.scroll-to-top {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: black;
  border-radius: 50%;
  cursor: pointer;
  transition: opacity 0.3s ease;
  z-index: 9998;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: var(--scroll-to-top-opacity, 0);
}
.scroll-to-top svg {
  display: block;
  width: 20px;
  height: 20px;
  stroke: white;
  transition: all 0.1s ease;
}
.scroll-progress-bar {
  position: fixed;
  bottom: 0;
  right: 0;
  width: var(--scroll-progress-width, 0%);
  height: 5px;
  background-color: #002060;
  z-index: 9999;
}
.hero-slogan {
  color: #ffffff !important;
}
.footer-text-override {
  color: #ffffff !important;
}
.white-logo-filter {
  filter: brightness(0) invert(1);
}
.footer-bg-override {
  background-color: #002060 !important;
}
